input[type="file"] {
    display: none;
}



.custom-file-upload {
    border: 1px dashed rgb(0, 0, 0);
    color:rgb(255, 255, 255);
    border-radius: 7px;
    display: inline-block;
    padding: 20px;
    cursor: pointer;
    background-color: lightblue;
}

.custom-file-upload:hover{
    opacity: 0.6;
}

.inputImg {
    background-image: url('../../assets/svg/cloud-upload-svgrepo-com.svg');
    background-repeat: no-repeat;
    padding:12px;
    padding-top: 1px;
    margin-right: 3px;
}

.inputText{
    font-family: VT323, monospace;
    font-weight: 600;
}